.header {
    background-image: url('../img/header-bg.png');
    height: 840px;
    position: relative;
    &__scroll {
        position: absolute;
        left: 48%;
        bottom: -33px;
        width: 66px;
        height: 66px;
        border: 1px solid #fff;
        border-radius: 50%;
        background-color: #fff;
        background-image: url('../img/scroll-down.png');
        background-repeat: no-repeat;
        background-position: center;
        box-shadow: 0 6px 22px rgba(0, 0, 0, 0.15);
        cursor: pointer;
        outline: none;
        @include r(600) {
            left: 43%; } } }
.nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
.logo {
    @include r(991) {
        margin: 0 auto; }
    @include r(557) {
        margin: 0; } }
.menu {
    @include r(991) {
        margin: 0 auto; }
    @include r(557) {
        flex-basis: 100%;
        height: 0;
        overflow: hidden;
        text-align: center;
        background-color: #00be9d;
        background-image: linear-gradient(to right, #00be9d 0%, #0082f9 100%);
        transition: height 1s ease;
        transition: height 1s ease;
        &_js {
            height: 235px;
            transition: height 1s ease; } }
    &__item {
        display: inline-block;
        @include r(557) {
            display: block; } }
    &__link {
        padding: 0 25px;
        color: #fffefe;
        font-family: Roboto;
        font-weight: 400;
        line-height: 44px;
        @include css-lock(10, 15, 320, 991); }
    &__log {
        padding: 0 25px;
        border-left: 1px solid #fff;
        color: #fffefe;
        font-family: Roboto;
        font-weight: 700;
        line-height: 44px;
        text-transform: uppercase;
        @include css-lock(10, 15, 320, 991);
        @include r(557) {
            border-left: 0; } }
    &__sign {
        padding: 17px 25px;
        color: #fffefe;
        font-family: Roboto;
        font-weight: 700;
        line-height: 44px;
        text-transform: uppercase;
        border-radius: 21px;
        border: 2px solid #fff;
        @include css-lock(10, 15, 320, 991); } }
.toggle {
    padding: 5px 10px 10px 10px;
    border: 1px solid #888;
    border-radius: 4px;
    display: none;
    @include r(557) {
        display: block; }
    &:hover {
        background-color: #fff; }
    &__item {
        margin-top: 5px;
        display: block;
        background-color: #888;
        width: 22px;
        height: 2px;
        border-radius: 1px; } }
.slider {
    &__content {
        margin-top: 129px; }
    &__text {
        margin: 63px 0;
        color: #e2f0ff;
        font-family: Roboto;
        font-weight: 400;
        line-height: 44px;
        @include css-lock(15, 22, 320, 991); } }
.h1 {
    text-shadow: 0 3px 12px rgba(0, 0, 0, 0.1);
    color: #fff;
    font-family: Montserrat;
    font-weight: 900;
    text-transform: uppercase;
    @include css-lock(30, 52, 320, 991); }
.button {
    padding: 20px 35px;
    box-shadow: 0 6px 22px rgba(0, 0, 0, 0.35);
    border-radius: 29px;
    background-color: #00be9d;
    color: #fff;
    font-family: Roboto;
    font-weight: 500;
    line-height: 44px;
    text-transform: uppercase;
    @include css-lock(10, 16, 320, 991);
    &_gradient {
        padding: 25px;
        background-image: linear-gradient(to right, #00be9d 0%, #0082f9 100%); } }
