}

.header__scroll {
  position: absolute;
  left: 48%;
  bottom: -33px;
  width: 66px;
  height: 66px;
  border: 1px solid #fff;
  border-radius: 50%;
  background-color: #fff;
  background-image: url("../img/scroll-down.png");
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 6px 22px rgba(0, 0, 0, 0.15);
  cursor: pointer;
  outline: none;
}

