.container {
	margin: 0 auto;
	@include r(767) {
		padding: 0 20px;
		width: 100%; }
	@include rmin(768) {
		width: 750px; }
	@include rmin(992) {
		width: 970px; }
	@include rmin(1200) {
		width: 1170px; } }
a {
	text-decoration: none; }
.clearfix {
	clear: both; }
section {
	padding: 90px 0 68px 0; }
